.about-product {
    &__wrapper {
        display: grid;
        grid-template-columns: 300px 1fr;
        gap: clamp(20px, 3vw, 40px);

        @media(max-width: 992px) {
            grid-template-columns: 1fr;
        }
    }

    &__aside {
        @media(max-width: 992px) {
            display: none;
        }
    }

    &__content {
        @media(max-width: 767px) {
            overflow: auto;
        }
    }

    &__block {
        display: grid;
        grid-template-columns: 265px 1fr;
        gap: 20px;
        margin-block: 30px;
        &-text {
            display: flex;
            flex-direction: column;
        }

        img {
            border-radius: 10px;
        }

        @media(max-width: 992px) {
            grid-template-columns: 1fr;
        }
    }

    p {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
    }

    &__docs {
      border-radius: 8px;
      padding: 20px;
      position: relative;
      background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
      margin-top: 40px;
      margin-bottom: 60px;
      @media(max-width: 992px) {
        padding-bottom: 0;
      }
      .left {
        display: flex;
        flex-direction: column;
        gap: 20px;
        max-width: 595px;
        .title {
            font-family: var(--font-family);
            font-weight: 700;
            font-size: clamp(22px, 3vw, 28px);
            text-transform: uppercase;
            color: var(--chernyy);
        }
        @media(max-width: 992px) {
            margin-bottom: 40px;
        }
      }

      .btn--accent {
        max-width: 170px;
      }

      .image {
        position: absolute;
        right: 50px;
        bottom: 0;
        @media(max-width: 992px) {
            position: static;
        }
        img {
            display: block;
        }
      }
    }

    &__popular {
        margin-bottom: 60px;
        .head {
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            gap: 10px;
            margin-bottom: 35px;
            justify-content: space-between;
        }

        &-title {
            font-family: var(--font-family);
            font-weight: 700;
            font-size: clamp(22px, 3vw, 28px);
            text-transform: uppercase;
            color: var(--chernyy);
        }

        &-items {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
            gap: 10px;

            @media(max-width: 767px) {
                grid-template-columns: 1fr 1fr;
            }
        }
    }

    .callback {
        &__form {
            max-width: 630px;
        }

        &__image {
            right: 0;
        }
    }

    &__areas {
        &-title {
            font-family: var(--font-family);
            font-weight: 700;
            font-size: clamp(22px, 3vw, 28px);
            text-transform: uppercase;
            color: var(--chernyy);
            display: flex;
            align-items: center;
            flex-wrap: wrap;
            gap: 20px;
            justify-content: space-between;
            margin-bottom: 35px;
            a {
                font-family: var(--font-family);
                font-weight: 400;
                font-size: 14px;
                line-height: 130%;
                color: var(--chernyy);
                border: 1px solid var(--siniy);
                border-radius: 8px;
                padding: 8px 20px;
                transition: .3s ease;
                text-transform: initial;
                @media(any-hover: hover) {
                    &:hover {
                        background: var(--siniy);
                        color: #fff;
                    }
                }
            }
        }
    }

    &__object {
        margin-bottom: 60px;
        &-title {
            font-family: var(--font-family);
            font-weight: 700;
            font-size: clamp(22px, 3vw, 28px);
            text-transform: uppercase;
            color: var(--chernyy);
        }

        .object__carts{
            @media(max-width: 992px) {
                display: flex;
                overflow: auto;
            }
        }
        .object__cart {
            @media(max-width: 992px) {
                flex-shrink: 0;
                max-width: 250px;
            }
        }
    }

    .company {
        

        &__images {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
    }
    
}